import { useTranslation } from "react-i18next";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { FileText, Loader2, ArrowRight } from "lucide-react";
import { Card, CardContent } from "@/components/ui/card";
import { Link } from "react-router-dom";
import { memo } from "react";
import PostGridCard from "@/components/community/PostGridCard";

interface GhostRestaurantRecentPostsProps {
  restaurantId: string;
}

const useRestaurantPosts = (restaurantId: string) => {
  return useQuery({
    queryKey: ["restaurant-posts", restaurantId],
    queryFn: async () => {
      // Get associated merchant user IDs
      const { data: merchants } = await supabase
        .from("merchant_restaurants")
        .select("user_id")
        .eq("restaurant_id", restaurantId);

      const merchantUserIds = merchants?.map(m => m.user_id) || [];
      
      // If no associated merchants, return empty
      if (merchantUserIds.length === 0) return [];

      // Get posts by associated merchants only
      const { data: posts, error } = await supabase
        .from("community_posts")
        .select(`
          *,
          author:profiles!community_posts_author_id_fkey(id, display_name, avatar_url),
          post_likes(user_id)
        `)
        .in("author_id", merchantUserIds)
        .eq("status", "approved")
        .order("created_at", { ascending: false })
        .limit(6);

      if (error) throw error;

      return (posts || []).map((post: any) => ({
        ...post,
        likes_count: post.likes_count || 0,
        user_liked: false,
      }));
    },
    enabled: !!restaurantId,
  });
};

export const GhostRestaurantRecentPosts = memo(({ restaurantId }: GhostRestaurantRecentPostsProps) => {
  const { t } = useTranslation();
  const { data: posts = [], isLoading } = useRestaurantPosts(restaurantId);

  if (isLoading) {
    return (
      <section className="py-8 md:py-12 lg:py-16">
        <div className="container mx-auto max-w-6xl px-4">
          <div className="flex items-center justify-center py-8">
            <Loader2 className="w-6 h-6 animate-spin text-primary" />
          </div>
        </div>
      </section>
    );
  }

  if (posts.length === 0) {
    return (
      <div className="mt-5 md:mt-6 space-y-2 md:space-y-3 bg-muted/50 rounded-xl p-4 md:p-5 border border-border/30">
        <h3 className="text-base md:text-lg font-semibold text-foreground flex items-center gap-2">
          <FileText className="w-4 h-4 md:w-5 md:h-5 text-primary" />
          {t("restaurants.recentPosts", "")}
        </h3>
        <p className="text-sm text-muted-foreground py-4 text-center">
          {t("restaurants.noRecentPosts", "")}
        </p>
      </div>
    );
  }

  return (
    <div className="mt-5 md:mt-6 space-y-2 md:space-y-3 bg-muted/50 rounded-xl p-4 md:p-5 border border-border/30">
      <h3 className="text-base md:text-lg font-semibold text-foreground flex items-center gap-2">
        <FileText className="w-4 h-4 md:w-5 md:h-5 text-primary" />
        {t("restaurants.recentPosts", "")}
      </h3>

      <div className="grid grid-cols-2 gap-2 md:gap-3">
        {posts.map((post: any) => (
          <PostGridCard key={post.id} post={post} />
        ))}
      </div>

      {posts.length >= 6 && (
        <div className="text-center pt-1">
          <Link 
            to="/community" 
            className="inline-flex items-center gap-1.5 text-xs text-primary hover:text-primary/80 font-medium transition-colors"
          >
            {t("common.viewMore", "")}
            <ArrowRight className="w-3.5 h-3.5" />
          </Link>
        </div>
      )}
    </div>
  );
});

GhostRestaurantRecentPosts.displayName = "GhostRestaurantRecentPosts";

export default GhostRestaurantRecentPosts;
